.page-footer {
  width: 100%;
  height: 235px;
}

.map {
  display: block;
  width: 320px;
  margin: 0 auto;
  text-align: center;
  background-image: url("../img/map.png");
  background-position: 70% 50%;
}

.page-footer__wrapper {
  width: 320px;
  margin: 0 auto;
  height: 235px;
  box-sizing: border-box;
  background-color: $main-color;
}

.footer-logo {
  display: block;
  text-align: center;
  margin: 0 auto;
  padding-top: 40px;
  background-color: $main-color;
}
