.page-header {
  width: 100%;
  height: 65px;
}

.page-header__wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 15px;
  display: flex;
  box-sizing: border-box;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

